* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Nunito", sans-serif;
}

html {
    scroll-behavior: smooth;
}

body {
    background: linear-gradient(-45deg, #07090a, #2f3437, #0a0d0f, #474c50);
    background-size: 400% 400%;
    animation: gradient 2s ease infinite;

    /* display: flex; */
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: rgb(0, 0, 0);
}

.main {
    width: 1010px;
}

.last {
    /* width: 1010px; */
    margin-top: 0px;
    height: fit-content;
    text-align: start;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 2;
    font-size: 15px;
    font-weight: bolder;
}

.navbar {
    border: 2px solid aqua;
    border-radius: 100px;
    width: 1010px;
    height: 60px;
    display: flex;
    justify-content: center;
    background-color: #2f3437;
    position: sticky;
    box-shadow: rgba(35, 28, 54, 0.25) 0px 30px 60px -12px inset,
        rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    top: 0%;
    color: aqua;
}

.head {
    width: 900px;
}

.under,
h5,
h4 {
    text-decoration: underline;
    border-left: 6px solid blue;
}

a {
    color: black;
}



@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    10% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}